// Library.js
import React, { useState } from 'react';
import TopNav from './TopNav';
import LibrarySong from './LibrarySong';
import './../styles/_topnav.scss';

const Library = ({ songs, setCurrentSong, audioRef, isPlaying, setSongs, libraryStatus }) => {
  // Estado para manejar el tipo de vista
  const [viewType, setViewType] = useState('grid'); // 'grid' o 'list'

  const toggleView = () => {
    setViewType(viewType === 'grid' ? 'list' : 'grid');
  };

  return (
    <div className={`library ${libraryStatus ? "active-library" : ""}`}>
      {/* Pasar el estado y la función de cambio de vista a TopNav */}
      <TopNav 
        title="Edits for DJs" 
        viewType={viewType}
        toggleView={toggleView} 
      />
      <div className={`library-songs ${viewType}`}>
        {songs.map(song => (
          <LibrarySong
            audioRef={audioRef}
            songs={songs}
            setSongs={setSongs}
            id={song.id}
            key={song.id}
            setCurrentSong={setCurrentSong}
            isPlaying={isPlaying}
            song={song}
            viewType={viewType} // Pasar el tipo de vista
          />
        ))}
      </div>
      <div className="fade"></div>
    </div>
  );
};

export default Library;
