import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlay,
  faAngleLeft,
  faAngleRight,
  faPause,
} from "@fortawesome/free-solid-svg-icons";

const Player = ({
  isPlaying,
  setIsPlaying,
  audioRef,
  songInfo,
  setSongInfo,
  currentSong,
  songs,
  setCurrentSong,
  setSongs,
  onPreloadNextTrack,
}) => {
  const [buffering, setBuffering] = useState(false);
  const [userInteracted, setUserInteracted] = useState(false);
  const [preloadTimeout, setPreloadTimeout] = useState(null);

  const activeLibraryHandler = (nextPrev) => {
    const newSongs = songs.map((song) => {
      return {
        ...song,
        active: song.id === nextPrev.id,
      };
    });
    setSongs(newSongs);
  };

  const trackAnim = {
    transform: `translateX(${songInfo.animationPercentage}%)`,
  };

  const getTime = (time) =>
    Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2);

  const dragHandler = (e) => {
    audioRef.current.currentTime = e.target.value;
    setSongInfo({ ...songInfo, currentTime: e.target.value });
  };

  const playSongHandler = () => {
    setUserInteracted(true);
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(false);
    } else {
      attemptPlayback();
    }
  };

  const attemptPlayback = () => {
    if (!buffering && userInteracted) {
      audioRef.current.play().catch((error) => {
        console.error("Error al reproducir el audio:", error);
        setTimeout(attemptPlayback, 1000);
      });
      setIsPlaying(true);
    }
  };

  useEffect(() => {
    if (!buffering && isPlaying && userInteracted) {
      attemptPlayback();
    }
  }, [buffering, userInteracted]);

  const skipTrackHandler = async (direction) => {
    let currentIndex = songs.findIndex((song) => song.id === currentSong.id);

    if (direction === "skip-forward") {
      await setCurrentSong(songs[(currentIndex + 1) % songs.length]);
    }
    if (direction === "skip-back") {
      if ((currentIndex - 1) % songs.length === -1) {
        await setCurrentSong(songs[songs.length - 1]);
        if (isPlaying) audioRef.current.play();
        return;
      }
      await setCurrentSong(songs[(currentIndex - 1) % songs.length]);
    }
    if (isPlaying) audioRef.current.play();
  };

  // Preload next track when 10 seconds are left in the current track
  useEffect(() => {
    if (audioRef.current && onPreloadNextTrack) {
      const handleTimeUpdate = () => {
        const remainingTime = audioRef.current.duration - audioRef.current.currentTime;
        if (remainingTime < 10) {
          const currentIndex = songs.findIndex((song) => song.id === currentSong.id);
          const nextTrack = songs[(currentIndex + 1) % songs.length];
          onPreloadNextTrack(nextTrack);
        }
      };

      audioRef.current.addEventListener('timeupdate', handleTimeUpdate);

      return () => {
        audioRef.current.removeEventListener('timeupdate', handleTimeUpdate);
      };
    }
  }, [currentSong, onPreloadNextTrack]);

  return (
    <div className="player">
      <div className="time-control">
        <p>{getTime(songInfo.currentTime)}</p>
        <div
          style={{
            background: `linear-gradient(to right, ${currentSong.color[0]},${currentSong.color[1]})`,
          }}
          className="track"
        >
          <input
            value={songInfo.currentTime}
            type="range"
            max={songInfo.duration || 0}
            min={0}
            onChange={dragHandler}
          />
          <div style={trackAnim} className="animate-track"></div>
        </div>
        <p>{songInfo.duration ? getTime(songInfo.duration) : "0:00"}</p>
      </div>
      <div className="play-control">
        <FontAwesomeIcon
          onClick={() => skipTrackHandler("skip-back")}
          className="skip-back"
          size="2x"
          icon={faAngleLeft}
        />
        <FontAwesomeIcon
          onClick={playSongHandler}
          className="play"
          size="2x"
          icon={isPlaying ? faPause : faPlay}
        />
        <FontAwesomeIcon
          className="skip-forward"
          size="2x"
          icon={faAngleRight}
          onClick={() => skipTrackHandler("skip-forward")}
        />
      </div>
      <audio
        ref={audioRef}
        onLoadedMetadata={(e) => setSongInfo({ ...songInfo, duration: e.target.duration })}
        onTimeUpdate={(e) => setSongInfo({ ...songInfo, currentTime: e.target.currentTime })}
        onWaiting={() => setBuffering(true)}
        onPlaying={() => setBuffering(false)}
        src={currentSong.audio}
        autoPlay={isPlaying}
      />
    </div>
  );
};

export default Player;
