// TopNav.js
import React from 'react';

const TopNav = ({ title, viewType, toggleView }) => {
    return (
        <div className="topnav">
            <button className="view-toggle" onClick={toggleView}>
                {viewType === 'grid' ? 'LIST' : 'ROW'}
            </button>
            <h1>{title}</h1>
        </div>
    );
};

export default TopNav;





