import React from 'react';

const LibrarySong = ({ songs, setCurrentSong, song, setSongs, id, isPlaying, audioRef, viewType }) => {
  const changeSongHandler = async () => {
    const selectedSong = songs.filter((state) => state.id === id);
    await setCurrentSong(selectedSong[0]);
    // Add active
    const newSongs = songs.map((song) => {
      if (song.id === id) {
        return {
          ...song,
          active: true,
        };
      } else {
        return {
          ...song,
          active: false,
        };
      }
    });
    setSongs(newSongs);
    if (isPlaying) audioRef.current.play();
  };

  return (
    <div
      onClick={changeSongHandler}
      className={`library-song ${song.active ? 'selected' : ''} ${viewType}`}
    >
      {viewType === 'grid' ? (
        // Vista en cuadrícula
        <>
          <img alt={song.name} src={song.cover} />
          <div className="song-description">
            <div className="song-texto">
              <h3>{song.name}</h3>
              <h4>{song.artist}</h4>
            </div>
            <div className="song-botones">
              <button>COMPRAR</button>
            </div>
          </div>
        </>
      ) : (
        // Vista en lista
        <>
          <img className="small-cover" alt={song.name} src={song.cover} />
          <div className="song-description">
            <div className="song-texto">
              <h3>{song.name}</h3>
              <h4>{song.artist}</h4>
            </div>
            <div className="song-botones">
              <button>COMPRAR</button>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default LibrarySong;
